html {
  font-size: 1rem;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #777;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.scroll-temp {
  margin: 0;
  padding: 0;
}

.weather-icon {
  width: 100px;
}
.rain-icon {
  width: 15px;
}

.daily-day {
  width: 25px;
}

.bg-opacity-2 {
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
}

.bg-opacity-5 {
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
}
.bg-opacity-8 {
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
}

.city-button {
  width: 7rem;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  box-shadow: black 1px 1px 1px;
}

.city-button:hover {
  background-color: rgba(255, 255, 255, 0.6);
  box-shadow: black 2px 2px 2px;
}

div.scrollmenu {
  /* background-color: rgb(94, 93, 93); */
  overflow: auto;
  white-space: nowrap;
}

div.scrollmenu div {
  display: inline-block;

  text-align: center;
  padding: 14px;
  text-decoration: none;
}

div.scrollmenu a:hover {
  background-color: #777;
}

@keyframes moveBg {
  0% {
    background-size: 100%;
  }

  50% {
    background-size: 130%;
  }

  100% {
    background-size: 100%;
  }
}

@media only screen and (max-width: 767px) {
  html {
    font-size: 0.5rem;
  }
  p {
    font-size: 0.8rem;
  }
  .weather-icon {
    width: 50px;
  }
  .daily-day {
    width: 30px;
  }
  .rain-icon {
    width: 8px;
  }
  .city-button {
    width: 8rem;
  }
}

@media only screen and (min-width: 768px) {
  html {
    font-size: 1.2rem;
  }
  p {
    font-size: 1rem;
  }

  .weather-icon {
    width: 100px;
  }
  .daily-day {
    width: 75px;
  }
}
